<template>
  <div :class="$style.wrapper">
    <el-dialog :title="revenueTitle" :visible.sync="revBoxVisible"  @close='closeDialog'>
      <el-form :model="revenue">
        <el-form-item label="" :label-width="formLabelWidth">
          <div :class="$style.select">
            <el-select v-model="RevenueTypeName" placeholder="" @change="chooseRevType(RevenueTypeName)">
                <el-option
                  v-for="item in RevenueTypeNameOpts"
                  :key="item.RevenueTypeCode"
                  :label="item.RevenueTypeName"
                  :value="item.RevenueTypeName">
                </el-option>
              </el-select>
          </div>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="revenue.ChargeProjectName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <div :class="$style.select">
            <el-select v-model="CalculateUnitName" placeholder="" @change="chooseCalUnit(CalculateUnitName)">
              <el-option
                v-for="item in CalculateUnitNameOpts"
                :key="item.CalculateUnitCode"
                :label="item.CalculateUnitName"
                :value="item.CalculateUnitName">
              </el-option>
            </el-select>
          </div>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="revenue.ChargeStandard" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <div :class="$style.select">
            <el-select v-model="ChargeScopeName" placeholder="" @change="chooseChargeScope(ChargeScopeName)">
              <el-option
                v-for="item in ChargeScopeNameOpts"
                :key="item.ChargeScopeCode"
                :label="item.ChargeScopeName"
                :value="item.ChargeScopeName">
              </el-option>
            </el-select>
          </div>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" v-if="showBudget">
          <el-input v-model="revenue.BudgetTotalRrevenue" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" v-if="showActual">
          <el-input v-model="revenue.ActualTotalRrevenue" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="200" :label-width="formLabelWidth">
          <el-input v-model="revenue.BudgetAccding" 
            autocomplete="off" 
            type="textarea"
            :rows="5">
          </el-input>
        </el-form-item>
        <el-form-item label="200" :label-width="formLabelWidth">
          <el-input v-model="revenue.Notes" 
            autocomplete="off" 
            type="textarea"
            :rows="5">
          </el-input>
        </el-form-item>
        <el-form-item label="500" :label-width="formLabelWidth">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="/api/add/BudgetTotalRrevenue"
            accept=".xls,.xlsx,.doc,.docx"
            :auto-upload="false"
            :on-change="loadFile"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            :multiple="false"
            :limit="1"
            :on-exceed="handleExceed"
            :file-list="fileList">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">: 10 MB(docxlsdocxxlsx</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="subRevCancel"> </el-button>
        <el-button type="primary" @click="subRevenue"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props:{
    revBoxVisible:{
      type:Boolean,
      default:false
    },
    revenueTitle:{
      type:String,
      default:''
    },
    revenue:{
      type:Object,
      default:function(){
        return {
          ChargeProjectName:'',
          ChargeStandard:'',
          BudgetTotalRrevenue:'',
          ActualTotalRrevenue:'',
          BudgetAccding:'',
          Notes:''
        }
      }
    },
    RevenueTypeNameOpts:{
      type:Array,
      default:function(){
        return [{
          "RevenueTypeCode":'', 
          "RevenueTypeName": ''
        }]
      }
    },
    CalculateUnitNameOpts:{
      type:Array,
      default:function(){
        return [{
          "CalculateUnitCode": '',
          "CalculateUnitName": ''
        }]
      }
    },
    ChargeScopeNameOpts:{
      type:Array,
      default:function(){
        return [{
          "ChargeScopeCode": '', 
          "ChargeScopeName": ''
        }]
      }
    }
  },
  data () {
    return {
      fileName:"",
      fileList: [],
      formLabelWidth: '210px',
      RevenueTypeName:'',
      CalculateUnitName:'',
      ChargeScopeName:'',
      BudgetAccding:'',
      revenueTitle:'',
      showBudget:false,
      showActual:false,
      RevenueTypeNameOpts:[],
      CalculateUnitNameOpts:[],
      ChargeScopeNameOpts:[],
      revenue:{
        ChargeProjectName:'',
        ChargeStandard:'',
        BudgetTotalRrevenue:'',
        ActualTotalRrevenue:'',
        BudgetAccding:'',
        Notes:''
      },
    };
  },
  //DOM
  updated(){
    this.showBudOrAct();
  },
  methods: {
    //
    showBudOrAct(){
      if(this.revenueTitle==''||this.revenueTitle==''){
        this.showBudget=true;
        this.showActual=false;
      }
      if(this.revenueTitle==''||this.revenueTitle==''){
        this.showBudget=false;
        this.showActual=true;
      }
    },
    //
    chooseRevType(RevenueTypeName){
      this.RevenueType=this.RevenueTypeNameOpts.filter((item)=>{
        return item.RevenueTypeName==RevenueTypeName;//return
      })[0];
    },
    //
    chooseCalUnit(CalculateUnitName){
      this.CalculateUnit=this.CalculateUnitNameOpts.filter((item)=>{
        return item.CalculateUnitName==CalculateUnitName;
      })[0];
    },
    //
    chooseChargeScope(ChargeScopeName){
      this.ChargeScope=this.ChargeScopeNameOpts.filter((item)=>{
        return item.ChargeScopeName==ChargeScopeName;
      })[0]
    },
    //
    subRevenue(){
      let year=new Date().getFullYear();
      if(this.showBudget){
        delete this.revenue['ActualTotalRrevenue'];
        year+=1
      }
      else if(this.showActual){
        delete this.revenue['BudgetTotalRrevenue'];
      }
      const revenueData={ApplyDpartmentID:this.$store.state.userId, ApplyDpartmentName:this.$store.state.userName, Annual:year,...this.revenue,...this.RevenueType,...this.CalculateUnit,...this.ChargeScope};
      if(this.fileName){
        revenueData.FileName=this.fileName;
      }
      this.$refs.upload.submit();
      this.fileList=[];//
      this.RevenueTypeName='';
      this.CalculateUnitName='';
      this.ChargeScopeName='';
      this.$emit('subRevenue',revenueData);
      this.fileName='';
    }, 
    //
    subRevCancel(){
      this.fileName='';
      this.$emit('subRevCancel');
    },
    //
    loadFile(file,fileList){
      this.fileName=file.name;
      this.fileList=fileList;
    },
    //
    handleExceed(files, fileList) {
      this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
    },
    //
    beforeRemove(file, fileList) {
      return this.$confirm(` ${ file.name }`);
    },
    //
    closeDialog(){
      this.fileName='';
      this.$emit('closeRevenue');
    }
  }
}
</script>

<style lang="scss" module>
.wrapper{
  i{
    position: absolute;
    right: 10px;
  }
  button{
    width:140px;
  }
  .select{
    text-align: left;
  }
}
</style>


