<template>
  <div :class="$style.wrapper">
    <h3>{{reportTitle}}</h3>
    <div :class="$style.select">
      <div :class="$style.elselect" v-if="depFlag">
        <span></span>
        <el-select v-model="DepartmentName" placeholder="" @change="chooseName(DepartmentName)">
          <el-option
            v-for="item in departments"
            :key="item.DepartmentID"
            :label="item.DepartmentName"
            :value="item.DepartmentName">
          </el-option>
        </el-select>
      </div>
      <div :class="$style.elselect" v-if="annualFlag">
        <span></span>
        <el-select v-model="Annual" placeholder="">
          <el-option
            v-for="item in years"
            :key="item.Annual"
            :label="item.Annual"
            :value="item.Annual">
          </el-option>
        </el-select>
      </div>
      <el-button type="primary" @click="checkReport"></el-button>
      <el-button type="success" @click="exportToExcel">excel</el-button>
    </div>  
    <div>
      <el-table
        id="reportForm"
        :data="reportData.data"
        :span-method="arraySpanMethod"
        height="500"
        border
        style="width: 100%">
        <el-table-column
          align="center"
          v-for="(value,key) in reportData.data[0]"
          :prop="key"
          :key="key">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import FileSaver from 'file-saver'
import Excel from 'xlsx'
import qs from 'qs'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      DepartmentName:'',
      Annual:'',
      DepartmentName:'',
      tableDepName:'',
      department:{},
      firColSpanArr:[],
      // secColSpanArr:[],
      thirColSpanArr:[],
      fourColSpanArr:[],
      departments:[],
      years:[],
      reportData:{},
      checked:false,
      repTitles:[
        '',
        '',
        '',
        '()',
        '()',
        '()',
        '()',
        '',
        '',
        '',
        '()',
        '()'
      ]
    };
  },
  watch:{//
    departments:{
      handler:function(val,oldVal){
        if(val.length==0){//val==[]
          this.DepartmentName='';
          this.Annual='';
        }
      },
      immediate:true
    },
    reportData:{
      handler:function(val,oldVal){
        //
        this.firColSpanArr=[];
        // this.secColSpanArr=[];
        this.thirColSpanArr=[];
        this.fourColSpanArr=[];
        if(val.data){
          //this.reportLen=val.data.length;//
          if(this.reportTitle==this.repTitles[3]||this.reportTitle==this.repTitles[4]||this.reportTitle==this.repTitles[5]||this.reportTitle==this.repTitles[6]||this.reportTitle==this.repTitles[10]||this.reportTitle==this.repTitles[11]){
            this.getSpanArr(val.data,'',this.firColSpanArr);
            this.getSpanArr(val.data,'',this.thirColSpanArr);
            this.getSpanArr(val.data,'',this.fourColSpanArr);
          }else{
            this.getSpanArr(val.data,'',this.firColSpanArr);
            this.getSpanArr(val.data,'',this.thirColSpanArr);
            this.getSpanArr(val.data,'',this.fourColSpanArr);
          }
        }
      },
      immediate:true
    },
    reportTitle:{
      handler:function(val,oldVal){
        this.reportData.data=[];//
        this.Annual='';
        this.DepartmentName='';
        if(!this.$store.state.depFlag && this.$store.state.annualFlag){//
          this.annualPost=true;
          this.reportPost=false;
          let postData=null;
          if(this.$store.state.userId!='000'){
            postData=qs.stringify({
              DepartmentID:this.$store.state.userId
            })
          }
          let yearCallBack=(response)=>{
            this.years=response.data;
          }
          this.postTable(val,this.annualPost,this.reportPost,postData,yearCallBack);
        }else if(this.$store.state.depFlag){//
          let callback=(response)=>{
          this.departments=response.data;
          }
          this.post('/api/departmentname',null,callback);
        }
      },
      immediate:true
    }
  },
  computed:{//datamapStatestore.state.reportTitlethis.reportTitle
    ...mapState(['reportTitle','depFlag','annualFlag'])
  },
  methods: {
    //
    postTable(title,annualPost,reportPost,postData,callback){
       this.repTitles.forEach((item,index)=>{
         if(title==item){
            if(annualPost){
             this.post(`/api/table${index+1}/annual`,postData,callback);
            }else if(reportPost){
              this.post(`/api/table${index+1}/details`,postData,callback);
            }else{
              this.post('/api/table8/details',postData,callback);
            }
         }
       })
    },
    //
    getSpanArr(arr,property,storeArr){
      let divIndex=0;
      if(arr){
        arr.forEach((item,index)=>{
          if(index===0){
            storeArr.push(1);
          }else{
            if(item[property]==arr[index-1][property]){
              storeArr[divIndex]+=1;
              storeArr.push(0);
            }else{
              divIndex=index;
              storeArr.push(1);
            }
          }
        })
      }
    },
    //
    mergesCell(colIndex,spanArr){
      let mergesArr=[];
      let newSpanArr=spanArr.concat([]);//concatspanArrnewSpanArrspanArrsplice
      let i=this.DepartmentName==''?3:4;
      if(this.DepartmentName){
        newSpanArr.splice(0,3);
      }else{
        newSpanArr.splice(0,2);
      }
      newSpanArr.forEach((value,index)=>{
        if(value){
          mergesArr[mergesArr.length]={
            s:{},
            e:{}
          }
          mergesArr[mergesArr.length - 1].s.c = colIndex;
          mergesArr[mergesArr.length - 1].s.r = index+i;
          mergesArr[mergesArr.length - 1].e.c = colIndex;
          mergesArr[mergesArr.length - 1].e.r = index+i+value-1;
        } 
      })
      return mergesArr;
    },
    //
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if(rowIndex==0){
        return {
          rowspan:1,
          colspan:Object.keys(this.reportData.data[0]).length
        }
      }
      if(this.DepartmentName){
        if(rowIndex==1){
          return {
            rowspan:1,
            colspan:Object.keys(this.reportData.data[0]).length
          }
        }
      }
      if(this.reportTitle!==this.repTitles[7]){
        if(columnIndex ===0 || columnIndex ===1){
          if(this.firColSpanArr[rowIndex]){
            return {
              rowspan:this.firColSpanArr[rowIndex],
              colspan:1
            }
          }else{
            return {
              rowspan:0,
              colspan:0
            }
          }
        }
        if(columnIndex ===2){
          if(this.thirColSpanArr[rowIndex]){
            return {
              rowspan:this.thirColSpanArr[rowIndex],
              colspan:1
            }
          }else{
            return {
              rowspan:0,
              colspan:0
            }
          }
        }
        if(columnIndex ===3){
          if(this.fourColSpanArr[rowIndex]){
            return {
              rowspan:this.fourColSpanArr[rowIndex],
              colspan:1
            }
          }else{
            return {
              rowspan:0,
              colspan:0
            }
          }
        }
      }
    },
    //
    chooseName(DepartmentName){
      this.reportData.data=[];
      this.Annual='';//
      //ID
      this.department=this.departments.filter((item)=>{
        return item.DepartmentName==DepartmentName;
      })[0];
      this.annualPost=true;
      this.reportPost=false;
      let postData=qs.stringify(this.department);
      let callback=(response)=>{
        this.years=response.data;
      }
      this.postTable(this.reportTitle,this.annualPost,this.reportPost,postData,callback);    
    },
    //
    checkReport(){
      let table=document.getElementById('reportForm');
      let header=table.getElementsByClassName('el-table__header-wrapper')[0];
      header.style.cssText="display:none";
      console.log(header)
      this.checked=true;
      if(this.reportTitle!==this.repTitles[7]){
        if(this.Annual){
          let checkData={year:this.Annual};
          if(this.departments.length){//
            checkData={...this.department,year:this.Annual};
          }else if(this.$store.state.userId!='000'){
            checkData={
              DepartmentName:this.$store.state.userName,
              DepartmentID:this.$store.state.userId,
              year:this.Annual
            }
          } 
          this.reportPost=true;
          this.annualPost=false;
          let postData=qs.stringify(checkData);
          let callback=(response)=>{
            this.reportData=response.data;
          }
          this.postTable(this.reportTitle,this.annualPost,this.reportPost,postData,callback);
        }else{
          this.$message({
            message: '',
            type: 'warning'
          });
        } 
      }else{
        let callback=(response)=>{
          this.reportData=response.data;
        }
        this.postTable(this.reportTitle,null,null,null,callback);
      }
    },
    //
    getCols(){
      let cols=[];
      let keys=Object.keys(this.reportData.data[0]);
      for(let i=0;i<keys.length;i++){
        if(keys[i]!==''&&keys[i]!==''&&keys[i]!==''){
          cols.push({
            wpx:80
          });
        }
        if(keys[i]==''&& this.reportTitle!=this.repTitles[8]){
          cols.push({
            wpx:160
          })
        }
        if(keys[i]==''&& this.reportTitle==this.repTitles[8]){
          cols.push({
            wpx:250
          })
        }
        if(keys[i]==''||keys[i]==''){
          cols.push({
            wpx:85
          })
        }
      }
      return cols;
    },
    //excelABC...
    generateBigLet(){
      let keys=Object.keys(this.reportData.data[0]);//json
      let colNums=keys.length;
      let str=[];
      for(let i=65;i<65+colNums;i++){
        str.push(String.fromCharCode(i));
      }
      return str;
    },
    //
    addCellSty(sheet){
      let workArea=this.generateBigLet();
      let workLen=workArea.length;
      let merge1=this.mergesCell(0,this.firColSpanArr);
      let merge2=this.mergesCell(1,this.firColSpanArr);
      let merge3=this.mergesCell(2,this.thirColSpanArr);
      let merge4=this.mergesCell(3,this.fourColSpanArr);
      let mergeArr=merge1.concat(merge2).concat(merge3).concat(merge4);
      const border= { 
        top: { style: "thin"}, 
        right: { style: "thin"}, 
        bottom: { style: "thin"}, 
        left: { style: "thin"} 
      };
      const align={
        horizontal: "center" ,
        vertical: "center",
        wrapText:true
      }
      if(this.DepartmentName){
          sheet["A3"]={
            v:`${this.DepartmentName}`,
            t:'s',
          }
          mergeArr.push({
            s:{
              c:0,r:2,
            },
            e:{
              c:workLen-1,
              r:2
            }
          })
        }
      sheet["A1"]={
        v:`${this.reportData.title}`,
        t:'s',
        s:{
          alignment:align
        }
      }
      mergeArr.push({
        s:{
          c:0,r:0,
        },
        e:{
          c:workLen-1,
          r:1
        }
      })   
      sheet["!merges"]=mergeArr;
      workArea.forEach((value,index)=>{
        let rows=this.$store.state.userId!=='000'?this.reportData.data.length-2:this.reportData.data.length+1;//+1sheet
        let i=this.DepartmentName===''?3:4;
        for(;i<=rows;i++){
          if(sheet[value+i]){//
            sheet[value+i].s={
              border:border,
              alignment:align
            }
          }
        }
      });
    },
    //excel
    exportToExcel(){
      if(this.checked){
        let cols=this.getCols();
        /* generate workbook object from table */
        let sheet =Excel.utils.json_to_sheet(this.reportData.data);
        sheet['!cols'] = cols;
        this.addCellSty(sheet);
        try {
          FileSaver.saveAs(this.sheet2blob(sheet), `${this.reportData.title}.xlsx`)
        } catch (e) { if (typeof console !== 'undefined') console.log(e) }
      }else{
        this.$message({
          message: '~',
          type: 'warning'
        });
      }
    },
    // ArrayBuffer
    s2ab(s) {
      let buf = new ArrayBuffer(s.length);
      let view = new Uint8Array(buf);
      for (let i = 0; i !== s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
      return buf;
    },
    sheet2blob(sheet, sheetName) {
      sheetName = sheetName || 'sheet1';
      var workbook = {
        SheetNames: [sheetName],
        Sheets: {}
      };
      workbook.Sheets[sheetName] = sheet;
      console.log(workbook.Sheets[sheetName])
      // excel
      var wopts = {
        bookType: 'xlsx', // 
        bookSST: false, // Shared String TableIOS
        type: 'binary'
      };
      var wbout = XLSX.write(workbook, wopts);//XLSXfull.min.js,xlsx
      var blob = new Blob([this.s2ab(wbout)], {type:"application/octet-stream"});
      return blob;
    }
  }
}

</script>
<style lang='scss' module>
.wrapper{
  background-color:#fff;
  color:#5c87b2;
  padding-top:20px;
  h3{
    margin:0px;
  }
  .select{
    padding:20px 0px;
    display:flex;
    flex-direction:row;
    justify-content:center;
    .elselect{
      margin-right:30px;
      >span{
        margin:0px 15px;
      }
    }
  }
}
</style>


