<template>
  <div :class="$style.wrapper">
    <el-dialog title="" :visible.sync="detailVisible" @close="closeDetail">
      <el-form :model="detail" ref="detailForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="detail.ProjectBudgetName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="detail.ProjectBudgetMoney" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="200" :label-width="formLabelWidth">
          <el-input v-model="detail.BudgetAccding" 
            autocomplete="off" 
            type="textarea"
            :rows="5">
          </el-input>
        </el-form-item>
        <el-form-item label="200" :label-width="formLabelWidth">
          <el-input v-model="detail.Notes" 
            autocomplete="off" 
            type="textarea"
            :rows="5">
          </el-input>
        </el-form-item>
      </el-form>
      <p :class="$style.illustrate">500</p>
      <el-upload
        ref="upload"
        class="upload-demo"
        action="/api/add/details"
        accept=".xls,.xlsx,.doc,.docx"
        :auto-upload="false"
        :on-change="loadFile"
        :before-remove="beforeRemove"
        :multiple="false"
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">: 10 MB(docxlsdocxxlsx</div>
      </el-upload>
      <div slot="footer" class="dialog-footer">
        <el-button @click="subDetailCancel"> </el-button>
        <el-button type="primary" @click="subDetail"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props:{
    detailVisible:{
      type:Boolean,
      default:false
    },
    detail:{
      type:Object,
      default:function(){
        return {
          //ProjectID:'',
          Annual:new Date().getFullYear()+1,
          BudgetAccding:'',
          Notes:'',
          ProjectBudgetMoney:'',
          ProjectBudgetName:'',
          Illustration:''
        }
      }
    }
  },
  data () {
    return {
      fileName:'',
      fileList: [],
      formLabelWidth: '210px',
      detail: {
        BudgetAccding:'',
        Notes:'',
        ProjectBudgetMoney:'',
        ProjectBudgetName:'',
      },
    };
  },
  methods: {
    //
    clearAll(){
      this.detail={
        BudgetAccding:'',
        Notes:'',
        ProjectBudgetMoney:'',
        ProjectBudgetName:''
      };
    },
    subDetail(){
      if(this.fileName)
      {       
        this.detail.FileName=this.fileName;
      }
      this.$refs.upload.submit();
      this.fileList=[];//
      this.fileName='';//
      this.$emit('subDetail',this.detail);
    },
    subDetailCancel(){
      this.fileName='';
      this.$emit('subDetailCancel');
    },
    loadFile(file,fileList){
      this.fileName=file.name;
      this.fileList=fileList;
    },
    handleExceed(files, fileList) {
      this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${ file.name }`);
    },
    closeDetail(){
      this.clearAll();
      this.fileName='';
      this.$emit('closeDetail');
    },
  }
}

</script>
<style lang="scss" module>
.wrapper{
  i{
    position: absolute;
    right: 10px;
  }
  button{
    width:140px;
  }
  .illustrate{
    margin:15px 0px;
    font-weight: bold;
  }
}
</style>


